import { instantiate, Prefab, Vec3, Node, UITransform } from "cc";
import { EntityManager } from "../core/EntityManager";
import { PlayerTag } from "../components/Tag/PlayerTag";
import { PrefabNames } from "../data/consts";
import { EnemyTag } from "../components/Tag/EnemyTag";
import { TileTag } from "../components/Tag/TileTag";
import { Position } from "../components/Position";
import { ViewComponent } from "../components/core/ViewComponent";
import { SceneController } from "../core/SceneController";
import { PrefabType } from "../data/types";

export class RenderSystem {
    constructor(private entityManager: EntityManager,
                private prefabs: Record<PrefabType, Prefab>,
                private gameRoot: Node,
                private tilesLayer: Node,
                private entityLayer: Node,
                private tileSize: number
    ){}

    getPrefabName(entityId: number): PrefabType | null {
        if (this.entityManager.hasComponent(entityId, PlayerTag)) return PrefabNames.Player;
        if (this.entityManager.hasComponent(entityId, EnemyTag)) return PrefabNames.Enemy;
        if (this.entityManager.hasComponent(entityId, TileTag)) return PrefabNames.Tile;
        return null;
    }

    render(){
        const entities = this.entityManager.getEntityWithComponent(Position);

        for (const id of entities) {
            const prefabName = this.getPrefabName(id);
            if (!prefabName) continue;
            const position = this.entityManager.getComponent(id, Position);
            if (!position) continue;

            let view = this.entityManager.getComponent(id, ViewComponent);
            if(!view){
                console.log(prefabName);
                view = new ViewComponent(prefabName);
                this.entityManager.addComponent(id, view, ViewComponent);
                
                const prefab = this.prefabs[prefabName];
                const node = instantiate(prefab);
                node.setPosition(new Vec3(position.x * this.tileSize, position.y * this.tileSize, 0));

                /*if (prefabName === 'Tile') node.getComponent(UITransform).priority = 0;
                else if (prefabName === 'Enemy') node.getComponent(UITransform).priority = 1;
                else if (prefabName === 'Player') node.getComponent(UITransform).priority = 2;*/
                
                /*switch (prefabName) {
                    case PrefabNames.Tile:
                        node.setSiblingIndex(0);
                        break;
                    case PrefabNames.Enemy:
                        node.setSiblingIndex(1);
                        break;
                    case PrefabNames.Player:
                        node.setSiblingIndex(entities.length - 1);
                        break;
                }*/
                
                if (prefabName === PrefabNames.Tile) 
                    node.on(Node.EventType.TOUCH_END, () => {
                        SceneController._instance?.onTileTapped(position);
                    });

                if (prefabName === PrefabNames.Player) 
                    //node.setSiblingIndex(this.gameRoot.children.length - 1);
                    this.gameRoot.addChild(node);
                else if (prefabName === PrefabNames.Tile) 
                    this.tilesLayer.addChild(node);
                else if (prefabName === PrefabNames.Enemy) 
                    this.entityLayer.addChild(node);
                
                view.node = node;    

                //console.log('add ' + prefabName + position.x + position.y)
                //console.log("Added node", node.name, "at", node.getPosition());
                //console.log(node.isValid, node.activeInHierarchy, node.getWorldPosition());
            }
        }
    }

    update(dt: number) {
        const entities = this.entityManager.getEntityWithComponent(ViewComponent);
        for (const id of entities) {
            const view = this.entityManager.getComponent(id, ViewComponent);
            const pos  = this.entityManager.getComponent(id, Position);
            if (view?.node && pos) {
                view.node.setPosition(
                    new Vec3(pos.x * this.tileSize, pos.y * this.tileSize, view.node.getPosition().z)
                );
            }
        }
    }

    /*function inputFromVec2(entity: number, type: string, vec: Vec2) {
        return new InputComponent(entity, type as any, vec.x, vec.y);
    }*/
}

